@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:ital,wght@0,100;0,200;1,100;1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:ital,wght@0,100;0,200;1,100;1,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{background-color:#0a0a0a;height:5.5rem;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:100%;border-bottom:3px solid #fffcfc;font-size:calc(1.2em + 1.2vw);text-transform:uppercase;letter-spacing:0.3ch}.navbar p{margin-left:5rem;font-family:'IBM Plex Serif', serif;color:#f5f5f5}.menu-bars{margin-left:3rem;font-size:calc(.9em + .9vw);background:none;color:#b7fcfc}.nav-menu{background-color:#000000;width:11rem;height:100vh;display:-webkit-flex;display:flex;position:fixed;top:0;left:-100%;transition:850ms}.nav-menu.active{left:0rem;transition:350ms;margin-right:0;border-right:3px solid #fffcfc}.nav-text{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;list-style:none;height:5rem;margin-left:-3.3rem}.nav-text a{text-decoration:none;color:#f5f5f5;font-size:2.5rem;width:50%;height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;border-radius:4px;-webkit-justify-content:center;justify-content:center}.nav-text a:hover{background-color:#000d25}.instagram{transition:1900ms}.instagram a:hover{transition:1900ms;background-image:linear-gradient(to bottom right, #35177c, #b32b76);font-size:4rem}.twitter{transition:1900ms}.twitter a:hover{transition:1900ms;background-image:linear-gradient(to bottom right, #b0e2ee, #0896d8);font-size:4rem}.github{transition:1900ms}.github a:hover{transition:1900ms;background-image:linear-gradient(to bottom right, #ec6d05, #141413);font-size:4rem}.facebook{transition:1900ms}.facebook a:hover{transition:1900ms;background-image:linear-gradient(to bottom right, #0a0ddb, #03ebf3);font-size:4rem}.nav-menu-items{width:100%}.navbar-toggle{background-color:#000000;width:100%;height:80px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin-left:-2rem}

.body{height:100%;display:grid;grid-gap:0px;grid-template-columns:repeat(12, 1fr);grid-template-rows:20rem auto 60rem;background-color:#000203;background-image:url(/portafolio/static/media/giphy.501f5fcf.gif);background-repeat:no-repeat;background-attachment:fixed;background-position:center}.header{grid-column:7 / -1}.header .intro{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background-color:#0e152b;font-size:calc(1.2em + 1.2vw);text-transform:uppercase;letter-spacing:0.3ch;border:3px solid transparent;border-image:linear-gradient(to bottom right, #7bd2ec 0%, #fff 25%, #03f5a4 50%, #062af7 75%, #2ab1b1 100%);border-image-slice:1;padding-left:4rem;padding-right:3rem;-webkit-align-items:center;align-items:center;transition:500ms;margin-right:2rem;margin-top:2rem;transition:200ms}.header .intro:hover{transition:500ms;background-color:#3b4141}.header .intro:hover .titulo{color:#2ce9f0}.header .intro p{font-family:'Roboto', sans-serif;color:aliceblue}.header .intro .escom{transition:500ms;width:6em;text-align:center;box-shadow:rgba(113,225,240,0.25) 0px 30px 60px -12px inset,rgba(0,0,0,0.3) 0px 18px 36px -18px inset}.header .intro .escom:hover{box-shadow:rgba(247,248,248,0.25) 0px 30px 60px -12px inset,rgba(245,243,243,0.3) 0px 18px 36px -18px inset}.header .intro .escom a:hover{color:#75f1f5}.menu{grid-column:2 / 6}.menu .current{font-size:calc(.8em + .8vw);text-transform:uppercase;letter-spacing:0.3ch;text-align:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background-color:#03101f;border:3px solid transparent;border-image:linear-gradient(to bottom right, #7bd2ec 0%, #fff 25%, #03f5a4 50%, #062af7 75%, #2ab1b1 100%);border-image-slice:1;padding-left:4rem;padding-right:3rem;-webkit-align-items:center;align-items:center}.menu .current p{color:#ffffff;font-family:'Roboto', sans-serif}.menu .current:hover{transition:500ms;background-color:#3b4141}.menu .current:hover .titulo{color:#2ce9f0}.menu .list-active{transition:600ms;text-align:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background-color:#03101f;border:3px solid transparent;border-image:linear-gradient(to bottom right, #7bd2ec 0%, #fff 25%, #03f5a4 50%, #062af7 75%, #2ab1b1 100%);border-image-slice:1;padding-left:4rem;padding-right:4rem;-webkit-align-items:center;align-items:center}.menu .list-active ul{font-size:calc(.7em + .7vw)}.menu .list-active li{margin-bottom:1rem;color:#ffffff;font-family:'Roboto', sans-serif}.content{grid-column:6 / -1}.content .etc{padding-left:4rem;padding-right:3rem;margin-top:7rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.content .etc .b2{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;font-family:'Roboto', sans-serif;color:#ffffff;background-color:#0e152b;text-transform:uppercase;letter-spacing:0.3ch;font-size:calc(.5rem + .6vw);border:3px solid transparent;border-image:linear-gradient(to bottom right, #7bd2ec 0%, #fff 25%, #03f5a4 50%, #062af7 75%, #2ab1b1 100%);border-image-slice:1;padding-left:4rem;padding-right:4rem;margin-bottom:2rem;text-align:center;-webkit-align-items:center;align-items:center}.content .etc .b2:hover{background-color:#3b4141}.content .etc .b2:hover .obj{color:#2ce9f0}.footer{grid-column:4 / 9}.footer .about{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;font-family:'Roboto', sans-serif;color:#ffffff;background-color:#0e152b;text-transform:uppercase;letter-spacing:0.3ch;font-size:calc(.6em + .6vw);border:3px solid transparent;border-image:linear-gradient(to bottom right, #7bd2ec 0%, #fff 25%, #03f5a4 50%, #062af7 75%, #2ab1b1 100%);border-image-slice:1;padding-left:4rem;padding-right:4rem;padding-bottom:4rem;margin-bottom:2rem;text-align:center;-webkit-align-items:center;align-items:center;transition:600ms}.footer .about:hover{background-color:#3b4141}.footer .about:hover .hov{color:#2ce9f0}.footer .contact{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;font-family:'Roboto', sans-serif;color:#ffffff;background-color:#0e152b;text-transform:uppercase;letter-spacing:0.3ch;font-size:calc(.6em + .6vw);border:3px solid transparent;border-image:linear-gradient(to bottom right, #7bd2ec 0%, #fff 25%, #03f5a4 50%, #062af7 75%, #2ab1b1 100%);border-image-slice:1;padding-left:4rem;padding-right:4rem;margin-bottom:2rem;text-align:center;-webkit-align-items:center;align-items:center;transition:600ms}.footer .contact:hover{background-color:#3b4141}.footer .contact:hover .hov{color:#2ce9f0}.list-inactive{transition:600ms;position:absolute;left:-700px}.tit2{font-family:'IBM Plex Serif', serif}

